'use client';

// =============================================================================
// X402 CALLS DASHBOARD
// =============================================================================
// Customer-facing dashboard to view X402CallLog entries with detailed modal

import { useEffect, useState } from 'react';
import Link from 'next/link';
import { Zap, RefreshCw, X, ExternalLink, Copy, Check } from 'lucide-react';

interface X402CallLog {
  id: string;
  timestamp: string;
  customerId: string | null;
  endpointId: string;
  agentId: string | null;
  facilitator: string;
  chainId: string;
  asset: string;
  amount: string;
  status: 'success' | 'failed';
  errorCode?: string;
  x402TxHash?: string;
  latencyMs: number;
  resource: string;
}

interface LogsResponse {
  success: boolean;
  count: number;
  logs: X402CallLog[];
}

const ENDPOINT_ID = 'ep_url_enrich_demo_v1';
const REFRESH_INTERVAL = 5000; // 5 seconds

export default function X402Dashboard() {
  const [logs, setLogs] = useState<X402CallLog[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [lastRefresh, setLastRefresh] = useState<Date>(new Date());
  const [selectedLog, setSelectedLog] = useState<X402CallLog | null>(null);
  const [copiedField, setCopiedField] = useState<string | null>(null);

  const fetchLogs = async () => {
    try {
      setLoading(true);
      setError(null);

      // Use relative URL for API calls (works in both dev and production)
      const response = await fetch(`/api/x402/logs?endpointId=${ENDPOINT_ID}&limit=50`, {
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (!response.ok) {
        let errorMessage = `HTTP ${response.status}`;
        try {
          const errorData = await response.json();
          errorMessage = errorData.message || errorData.error || errorMessage;
        } catch (e) {
          // If JSON parsing fails, use status text
          errorMessage = response.statusText || errorMessage;
        }
        throw new Error(errorMessage);
      }

      const data: LogsResponse = await response.json();
      
      if (!data.success) {
        throw new Error(data.error || 'Failed to fetch logs');
      }
      
      setLogs(data.logs || []);
      setLastRefresh(new Date());
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to fetch logs';
      setError(errorMessage);
      console.error('Error fetching logs:', err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    // Initial fetch
    fetchLogs();

    // Set up auto-refresh
    const interval = setInterval(fetchLogs, REFRESH_INTERVAL);

    return () => clearInterval(interval);
  }, []);

  const formatTime = (timestamp: string) => {
    const date = new Date(timestamp);
    return date.toLocaleString();
  };

  const formatLatency = (ms: number) => {
    if (ms < 1000) {
      return `${ms}ms`;
    }
    return `${(ms / 1000).toFixed(2)}s`;
  };

  const formatAmount = (amount: string, asset: string) => {
    // Convert atomic units to human-readable
    // USDC has 6 decimals
    const decimals = asset === 'USDC' || asset.includes('0x') ? 6 : 6;
    const numAmount = BigInt(amount);
    const divisor = BigInt(10 ** decimals);
    const whole = numAmount / divisor;
    const remainder = numAmount % divisor;
    
    if (remainder === BigInt(0)) {
      return `${whole} ${asset}`;
    }
    
    const remainderStr = remainder.toString().padStart(decimals, '0');
    const trimmed = remainderStr.replace(/0+$/, '');
    return `${whole}.${trimmed} ${asset}`;
  };

  const formatChainId = (chainId: string) => {
    // Convert CAIP-2 format to readable name
    if (chainId.includes('8453')) return 'Base Mainnet';
    if (chainId.includes('84532')) return 'Base Sepolia';
    if (chainId.includes('1')) return 'Ethereum Mainnet';
    return chainId;
  };

  const getExplorerUrl = (txHash: string, chainId: string) => {
    if (chainId.includes('8453') || chainId.includes('84532')) {
      return `https://basescan.org/tx/${txHash}`;
    }
    if (chainId.includes('1')) {
      return `https://etherscan.io/tx/${txHash}`;
    }
    return null;
  };

  const copyToClipboard = async (text: string, field: string) => {
    try {
      await navigator.clipboard.writeText(text);
      setCopiedField(field);
      setTimeout(() => setCopiedField(null), 2000);
    } catch (err) {
      console.error('Failed to copy:', err);
    }
  };

  const formatTimestamp = (timestamp: string) => {
    const date = new Date(timestamp);
    return {
      date: date.toLocaleDateString(),
      time: date.toLocaleTimeString(),
      iso: date.toISOString(),
      relative: getRelativeTime(date),
    };
  };

  const getRelativeTime = (date: Date) => {
    const now = new Date();
    const diffMs = now.getTime() - date.getTime();
    const diffSecs = Math.floor(diffMs / 1000);
    const diffMins = Math.floor(diffSecs / 60);
    const diffHours = Math.floor(diffMins / 60);
    const diffDays = Math.floor(diffHours / 24);

    if (diffSecs < 60) return `${diffSecs} second${diffSecs !== 1 ? 's' : ''} ago`;
    if (diffMins < 60) return `${diffMins} minute${diffMins !== 1 ? 's' : ''} ago`;
    if (diffHours < 24) return `${diffHours} hour${diffHours !== 1 ? 's' : ''} ago`;
    return `${diffDays} day${diffDays !== 1 ? 's' : ''} ago`;
  };

  return (
    <div className="min-h-screen bg-infra-950 text-infra-100">
      {/* Navigation */}
      <nav className="sticky top-0 z-50 border-b border-infra-800 glass">
        <div className="max-w-6xl mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            <Link href="/" className="flex items-center gap-2">
              <div className="w-8 h-8 rounded-lg bg-gradient-coinbase flex items-center justify-center glow-coinbase">
                <Zap className="w-5 h-5 text-white" />
              </div>
              <span className="text-lg font-bold text-white">NexFlow</span>
            </Link>
            <div className="flex items-center gap-4">
              <Link href="/dashboard" className="text-sm text-infra-400 hover:text-infra-100 transition-colors">
                Dashboard
              </Link>
              <Link href="/docs" className="text-sm text-infra-400 hover:text-infra-100 transition-colors">
                API Docs
              </Link>
            </div>
          </div>
        </div>
      </nav>

      <div className="container mx-auto px-4 py-8 max-w-7xl">
        <div className="mb-6">
          <h1 className="text-3xl font-bold mb-2 text-white">x402 Calls Dashboard</h1>
          <p className="text-infra-400">
            Endpoint: <code className="bg-infra-800 px-2 py-1 rounded text-infra-200">{ENDPOINT_ID}</code>
          </p>
        </div>

      <div className="mb-4 flex items-center justify-between">
        <div className="flex items-center gap-4">
          <button
            onClick={fetchLogs}
            disabled={loading}
            className="px-4 py-2 bg-cyan-600 text-white rounded hover:bg-cyan-700 disabled:bg-infra-700 disabled:cursor-not-allowed flex items-center gap-2 transition-colors"
          >
            <RefreshCw className={`w-4 h-4 ${loading ? 'animate-spin' : ''}`} />
            {loading ? 'Refreshing...' : 'Refresh'}
          </button>
          <span className="text-sm text-infra-400">
            Last refreshed: {lastRefresh.toLocaleTimeString()}
          </span>
          <span className="text-sm text-infra-500">
            (Auto-refresh every {REFRESH_INTERVAL / 1000}s)
          </span>
        </div>
        <div className="text-sm text-infra-400">
          Total entries: <span className="font-semibold text-white">{logs.length}</span>
        </div>
      </div>

      {error && (
        <div className="mb-4 p-4 bg-red-900/20 border border-red-800 rounded text-red-400">
          <p className="font-semibold mb-2">Error: {error}</p>
          <p className="text-sm text-red-300">
            Check browser console for details. Verify the API endpoint is accessible at{' '}
            <code className="bg-red-900/30 px-1 rounded">/api/x402/logs?endpointId={ENDPOINT_ID}</code>
          </p>
        </div>
      )}

      {loading && logs.length === 0 ? (
        <div className="text-center py-8 text-infra-400">
          <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-cyan-400 mb-4"></div>
          <p>Loading logs...</p>
        </div>
      ) : logs.length === 0 && !error ? (
        <div className="text-center py-8 text-infra-400">
          <p className="text-lg mb-2">No logs found</p>
          <p className="text-sm mb-4">Make some calls to the endpoint to see logs here.</p>
          <p className="text-xs text-infra-500">
            Try: <code className="bg-infra-800 px-2 py-1 rounded">POST /api/v1/metered/url-enrich</code>
          </p>
        </div>
      ) : !error ? (
        <div className="overflow-x-auto">
          <table className="min-w-full bg-infra-900 border border-infra-800 rounded-lg">
            <thead className="bg-infra-800">
              <tr>
                <th className="px-4 py-3 text-left text-xs font-medium text-infra-300 uppercase tracking-wider">
                  Time
                </th>
                <th className="px-4 py-3 text-left text-xs font-medium text-infra-300 uppercase tracking-wider">
                  Status
                </th>
                <th className="px-4 py-3 text-left text-xs font-medium text-infra-300 uppercase tracking-wider">
                  Error Code
                </th>
                <th className="px-4 py-3 text-left text-xs font-medium text-infra-300 uppercase tracking-wider">
                  Latency
                </th>
                <th className="px-4 py-3 text-left text-xs font-medium text-infra-300 uppercase tracking-wider">
                  Facilitator
                </th>
                <th className="px-4 py-3 text-left text-xs font-medium text-infra-300 uppercase tracking-wider">
                  Amount
                </th>
                <th className="px-4 py-3 text-left text-xs font-medium text-infra-300 uppercase tracking-wider">
                  Tx Hash
                </th>
                <th className="px-4 py-3 text-left text-xs font-medium text-infra-300 uppercase tracking-wider">
                  Agent ID
                </th>
              </tr>
            </thead>
            <tbody className="divide-y divide-infra-800">
              {logs.map((log) => (
                <tr 
                  key={log.id} 
                  className="hover:bg-infra-800 transition-colors cursor-pointer"
                  onClick={() => setSelectedLog(log)}
                >
                  <td className="px-4 py-3 whitespace-nowrap text-sm text-infra-100">
                    {formatTime(log.timestamp)}
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap">
                    <span
                      className={`px-2 py-1 text-xs font-semibold rounded ${
                        log.status === 'success'
                          ? 'bg-green-900/30 text-green-400 border border-green-800'
                          : 'bg-red-900/30 text-red-400 border border-red-800'
                      }`}
                    >
                      {log.status}
                    </span>
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap text-sm text-infra-300">
                    {log.errorCode || '-'}
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap text-sm text-infra-300">
                    {formatLatency(log.latencyMs)}
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap text-sm text-infra-300">
                    {log.facilitator}
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap text-sm text-infra-300">
                    {formatAmount(log.amount, log.asset)}
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap text-sm text-infra-300 font-mono text-xs">
                    {log.x402TxHash ? (
                      <span className="truncate max-w-xs inline-block" title={log.x402TxHash}>
                        {log.x402TxHash}
                      </span>
                    ) : (
                      '-'
                    )}
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap text-sm text-infra-300">
                    {log.agentId || '-'}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : null}

      {/* Detail Modal */}
      {selectedLog && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4" onClick={() => setSelectedLog(null)}>
          <div className="bg-infra-900 border border-infra-800 rounded-lg max-w-2xl w-full max-h-[90vh] overflow-y-auto" onClick={(e) => e.stopPropagation()}>
            {/* Modal Header */}
            <div className="sticky top-0 bg-infra-900 border-b border-infra-800 px-6 py-4 flex items-center justify-between">
              <h2 className="text-xl font-bold text-white">Payment Details</h2>
              <button
                onClick={() => setSelectedLog(null)}
                className="text-infra-400 hover:text-infra-100 transition-colors"
              >
                <X className="w-5 h-5" />
              </button>
            </div>

            {/* Modal Content */}
            <div className="p-6 space-y-6">
              {/* Status Badge */}
              <div className="flex items-center gap-3">
                <span
                  className={`px-3 py-1 text-sm font-semibold rounded ${
                    selectedLog.status === 'success'
                      ? 'bg-green-900/30 text-green-400 border border-green-800'
                      : 'bg-red-900/30 text-red-400 border border-red-800'
                  }`}
                >
                  {selectedLog.status.toUpperCase()}
                </span>
                {selectedLog.errorCode && (
                  <span className="text-sm text-infra-400">
                    Error: <code className="text-red-400">{selectedLog.errorCode}</code>
                  </span>
                )}
              </div>

              {/* Timestamp */}
              <div>
                <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                  Timestamp
                </label>
                <div className="flex items-center gap-2">
                  <p className="text-sm text-infra-100">
                    {formatTimestamp(selectedLog.timestamp).date} at {formatTimestamp(selectedLog.timestamp).time}
                  </p>
                  <span className="text-xs text-infra-500">
                    ({formatTimestamp(selectedLog.timestamp).relative})
                  </span>
                  <button
                    onClick={() => copyToClipboard(selectedLog.timestamp, 'timestamp')}
                    className="ml-2 text-infra-400 hover:text-infra-100 transition-colors"
                    title="Copy ISO timestamp"
                  >
                    {copiedField === 'timestamp' ? (
                      <Check className="w-4 h-4 text-green-400" />
                    ) : (
                      <Copy className="w-4 h-4" />
                    )}
                  </button>
                </div>
              </div>

              {/* Payment Information */}
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                    Amount
                  </label>
                  <p className="text-sm text-infra-100 font-mono">
                    {formatAmount(selectedLog.amount, selectedLog.asset)}
                  </p>
                </div>
                <div>
                  <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                    Asset
                  </label>
                  <p className="text-sm text-infra-100">
                    {selectedLog.asset}
                  </p>
                </div>
                <div>
                  <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                    Network
                  </label>
                  <p className="text-sm text-infra-100">
                    {formatChainId(selectedLog.chainId)}
                  </p>
                </div>
                <div>
                  <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                    Facilitator
                  </label>
                  <p className="text-sm text-infra-100">
                    {selectedLog.facilitator}
                  </p>
                </div>
              </div>

              {/* Transaction Hash */}
              {selectedLog.x402TxHash && (
                <div>
                  <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                    Transaction Hash
                  </label>
                  <div className="flex items-center gap-2">
                    <code className="text-sm text-infra-200 font-mono bg-infra-800 px-2 py-1 rounded flex-1 truncate">
                      {selectedLog.x402TxHash}
                    </code>
                    <button
                      onClick={() => copyToClipboard(selectedLog.x402TxHash!, 'txHash')}
                      className="text-infra-400 hover:text-infra-100 transition-colors"
                      title="Copy transaction hash"
                    >
                      {copiedField === 'txHash' ? (
                        <Check className="w-4 h-4 text-green-400" />
                      ) : (
                        <Copy className="w-4 h-4" />
                      )}
                    </button>
                    {getExplorerUrl(selectedLog.x402TxHash, selectedLog.chainId) && (
                      <a
                        href={getExplorerUrl(selectedLog.x402TxHash, selectedLog.chainId)!}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="text-cyan-400 hover:text-cyan-300 transition-colors"
                        title="View on block explorer"
                      >
                        <ExternalLink className="w-4 h-4" />
                      </a>
                    )}
                  </div>
                </div>
              )}

              {/* Performance */}
              <div>
                <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                  Performance
                </label>
                <p className="text-sm text-infra-100">
                  Latency: <span className="font-mono">{formatLatency(selectedLog.latencyMs)}</span>
                </p>
              </div>

              {/* Identifiers */}
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                    Endpoint ID
                  </label>
                  <div className="flex items-center gap-2">
                    <code className="text-sm text-infra-200 font-mono bg-infra-800 px-2 py-1 rounded">
                      {selectedLog.endpointId}
                    </code>
                    <button
                      onClick={() => copyToClipboard(selectedLog.endpointId, 'endpointId')}
                      className="text-infra-400 hover:text-infra-100 transition-colors"
                    >
                      {copiedField === 'endpointId' ? (
                        <Check className="w-4 h-4 text-green-400" />
                      ) : (
                        <Copy className="w-4 h-4" />
                      )}
                    </button>
                  </div>
                </div>
                <div>
                  <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                    Log ID
                  </label>
                  <div className="flex items-center gap-2">
                    <code className="text-sm text-infra-200 font-mono bg-infra-800 px-2 py-1 rounded flex-1 truncate">
                      {selectedLog.id}
                    </code>
                    <button
                      onClick={() => copyToClipboard(selectedLog.id, 'logId')}
                      className="text-infra-400 hover:text-infra-100 transition-colors"
                    >
                      {copiedField === 'logId' ? (
                        <Check className="w-4 h-4 text-green-400" />
                      ) : (
                        <Copy className="w-4 h-4" />
                      )}
                    </button>
                  </div>
                </div>
              </div>

              {/* Optional Fields */}
              {(selectedLog.customerId || selectedLog.agentId) && (
                <div className="grid grid-cols-2 gap-4">
                  {selectedLog.customerId && (
                    <div>
                      <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                        Customer ID
                      </label>
                      <div className="flex items-center gap-2">
                        <code className="text-sm text-infra-200 font-mono bg-infra-800 px-2 py-1 rounded flex-1 truncate">
                          {selectedLog.customerId}
                        </code>
                        <button
                          onClick={() => copyToClipboard(selectedLog.customerId!, 'customerId')}
                          className="text-infra-400 hover:text-infra-100 transition-colors"
                        >
                          {copiedField === 'customerId' ? (
                            <Check className="w-4 h-4 text-green-400" />
                          ) : (
                            <Copy className="w-4 h-4" />
                          )}
                        </button>
                      </div>
                    </div>
                  )}
                  {selectedLog.agentId && (
                    <div>
                      <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                        Agent ID
                      </label>
                      <div className="flex items-center gap-2">
                        <code className="text-sm text-infra-200 font-mono bg-infra-800 px-2 py-1 rounded flex-1 truncate">
                          {selectedLog.agentId}
                        </code>
                        <button
                          onClick={() => copyToClipboard(selectedLog.agentId!, 'agentId')}
                          className="text-infra-400 hover:text-infra-100 transition-colors"
                        >
                          {copiedField === 'agentId' ? (
                            <Check className="w-4 h-4 text-green-400" />
                          ) : (
                            <Copy className="w-4 h-4" />
                          )}
                        </button>
                      </div>
                    </div>
                  )}
                </div>
              )}

              {/* Resource URL */}
              <div>
                <label className="text-xs font-medium text-infra-400 uppercase tracking-wider mb-1 block">
                  Resource URL
                </label>
                <div className="flex items-center gap-2">
                  <code className="text-sm text-infra-200 font-mono bg-infra-800 px-2 py-1 rounded flex-1 truncate">
                    {selectedLog.resource}
                  </code>
                  <button
                    onClick={() => copyToClipboard(selectedLog.resource, 'resource')}
                    className="text-infra-400 hover:text-infra-100 transition-colors"
                  >
                    {copiedField === 'resource' ? (
                      <Check className="w-4 h-4 text-green-400" />
                    ) : (
                      <Copy className="w-4 h-4" />
                    )}
                  </button>
                </div>
              </div>
            </div>

            {/* Modal Footer */}
            <div className="sticky bottom-0 bg-infra-900 border-t border-infra-800 px-6 py-4 flex justify-end">
              <button
                onClick={() => setSelectedLog(null)}
                className="px-4 py-2 bg-infra-800 text-infra-100 rounded hover:bg-infra-700 transition-colors"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
      </div>
    </div>
  );
}

